import React from 'react'
import Link from 'gatsby-link'

const PostListItem = ( props ) => (
        <li key={props.id}>
          <Link
            to={props.path}
          >
            <span>
              <b>{props.title}{" "}
              <span color="#BBB">â€” {props.date}</span></b>
            </span>
            <div>
              {props.foreword} 
            </div>
          </Link>
        </li>
)

export default PostListItem


