import React from 'react'
import Link from 'gatsby-link'
import Helmet from "react-helmet";
import PostListItem from '../../components/PostListItem';

const Journal = ({ data }) => (
  <div className="container card">
  <Helmet title={ 'Journal - ' + data.site.siteMetadata.title } />
    <div className="row">
      <div className="col-12">
      <h1 className="center">My Journal</h1>
      <p>Welcome to my writing place!</p>
      <p>My journal is a little bit different to all blogs you are used to. Here you will see all of my blogposts arranged in their respective categories. There are pages for each category where again only the relevant posts will be listed, take advantage of those pages because you will find extra information in them.</p>
      <p>Enjoy your stance!</p>
      <hr className="separator"/>
      </div>
    </div>

    <div className="row">
      <div className="col-12">
        <h2><Link to="/journal/python/">Python</Link></h2>
        <ol className="post-list">
        {data.python.edges.map(({ node }) =>
          <PostListItem 
            id={node.id}
            path={"/journal/"+ node.frontmatter.category +node.frontmatter.path}
            title={node.frontmatter.title}
            date={node.frontmatter.date}
            foreword={node.frontmatter.foreword}
          />
        )}
        </ol>  
      </div>
    </div>

    <div className="row">
      <div className="col-12">
        <h2><Link to="/journal/linux-administration/">Linux Administration</Link></h2>
        <ol className="post-list">
        {data.linuxad.edges.map(({ node }) =>
          <PostListItem 
            id={node.id}
            path={"/journal/"+ node.frontmatter.category +node.frontmatter.path}
            title={node.frontmatter.title}
            date={node.frontmatter.date}
            foreword={node.frontmatter.foreword}
          />
        )}
        </ol>
      </div>
    </div>

  </div>
)

export default Journal

export const query = graphql
`query JournalQuery {
  site {
    siteMetadata {
      title
    }
  }
  python: allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]} 
    filter: {frontmatter: { category: { eq: "python" } }} ) {
    edges {
      node {
        id
        timeToRead
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          path
          category
          foreword
        }
        excerpt
      }
    }
  }
  linuxad: allMarkdownRemark(sort: {order: DESC, fields: [frontmatter___date]} 
    filter: {frontmatter: { category: { eq: "linux-administration" } }} ) {
    edges {
      node {
        id
        timeToRead
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          path
          category
          foreword
        }
        excerpt
      }
    }
  }
}`



